  margin-left: 35px;
  font-style: italic;
  transition: 0.2s ease-in;
  color: #fff;
}

.logo {
  text-transform: uppercase;
  white-space: nowrap;
  color: #e3b873;
  font-weight: 400;
}

.nav {
  margin: 0 20px;
}
.nav.active .nav__list {
  left: 0;
  opacity: 1;
}
.nav__list {
  transition: 0.3s ease-in-out;
  display: flex;
  gap: 47px;
}
@media (max-width: 1199.98px) {
  .nav__list {
    gap: 30px;
  }
}
@media (max-width: 1039.98px) {
  .nav__list {
    opacity: 0;
    position: fixed;
    flex-direction: column;
    top: 88px;
    left: -100%;
    gap: 20px;
    color: black;
    overflow-y: auto;
    padding: 15px 15px;
    width: 300px;
    background-color: #fff;
  }
