.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #131313;
  z-index: 101;

  &__inner {
    padding: 22px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-size: 14px;
    @media (max-width: $breakpoint1200) {
      font-size: 11px;
    }
  }
  &__request-call {
    display: flex;
    align-items: center;
    @media (max-width: $breakpoint1040) {
      margin-left: auto;
    }
    @media (max-width: $breakpoint600) {
      display: none;
    }
  }

  &__phone {
    color: $accent-color;
    white-space: nowrap;
  }

  &__request {
    padding: 11px 22px;
    text-transform: uppercase;
    margin-left: 35px;
    font-style: italic;
    transition: 0.2s ease-in;
    color: #fff;
  }
}

.logo {
  text-transform: uppercase;
  white-space: nowrap;
  color: $accent-color;
  font-weight: 400;
}
.nav {
  margin: 0 20px;
  &.active &__list {
    left: 0;
    opacity: 1;
  }
  &__list {
    transition: 0.3s ease-in-out;
    display: flex;
    gap: 47px;
    @media (max-width: $breakpoint1200) {
      gap: 30px;
    }
    @media (max-width: $breakpoint1040) {
      opacity: 0;
      position: fixed;
      flex-direction: column;
      top: 88px;
      left: -100%;
      gap: 20px;
      color: black;
      overflow-y: auto;
      padding: 15px 15px;
      width: 300px;
      background-color: #fff;
    }
  }

  &__item {
    transition: 0.2s ease-in;
    &:hover {
      color: $accent-color;
    }
  }

  &__link {
    line-height: 15px;
    cursor: pointer;
  }
}

.burger {
  display: none;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  cursor: pointer;
  margin-left: 20px;

  @media (max-width: $breakpoint1040) {
    display: flex;
  }

  &.active {
    .burger__line {
      background: 0 0;
    }

    .burger__line::before {
      top: 0;
      transform: rotate(45deg);
    }

    .burger__line::after {
      top: 0;
      transform: rotate(-45deg);
    }
  }

  &__line {
    display: block;
    width: 26px;
    height: 3px;
    background-color: white;
    transition: all 0.3s ease-in-out;
    position: relative;

    &::before,
    &::after {
      content: "";
      left: 0;
      position: absolute;
      width: 26px;
      height: 3px;
      background-color: white;
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::before {
      top: -9px;
    }

    &::after {
      bottom: -9px;
    }
  }
}
